import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import { style } from '../common/utils/style';
import utils from '../common/utils/utils';
import { button } from '../mixins/basic/button';

import PressIcon from '../press-icon/press-icon.vue';
import PressLoading from '../press-loading/press-loading.vue';
import PressPopup from '../press-popup/press-popup.vue';


export default {
  name: 'PressActionSheet',
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  components: {
    PressIcon,
    PressPopup,
    PressLoading,
  },
  mixins: [button],
  props: {
    show: { type: Boolean, default: false },
    title: { type: String, default: '' },
    cancelText: { type: String, default: '' },
    description: { type: String, default: '' },
    round: {
      type: Boolean,
      default: true,
    },
    zIndex: {
      type: Number,
      default: 100,
    },
    actions: {
      type: Array,
      default: () => [],
    },
    overlay: {
      type: Boolean,
      default: true,
    },
    closeOnClickOverlay: {
      type: Boolean,
      default: true,
    },
    closeOnClickAction: {
      type: Boolean,
      default: true,
    },
    safeAreaInsetBottom: {
      type: Boolean,
      default: true,
    },
    buttonStyle: {
      type: [String, Object],
      default: '',
    },
    closeIconStyle: {
      type: [String, Object],
      default: '',
    },
    ...defaultProps,
  },
  emits: ['select', 'close', 'cancel', 'click-overlay', 'getuserinfo'],
  data() {
    return {

    };
  },
  computed: {
    innerButtonStyle() {
      return style(this.buttonStyle);
    },
    innerCloseIconStyle() {
      let result = {};













      return style([
        result,
        this.closeIconStyle,
      ]);
    },
  },
  methods: {
    getActionClass(item) {
      return `${utils.bem2('action-sheet__item', { disabled: item.disabled || item.loading })} ${item.className || ''}`;
    },
    getOpenType(item) {
      const { canIUseGetUserProfile } = this;
      return item.disabled || item.loading || (canIUseGetUserProfile && item.openType === 'getUserInfo') ? '' : item.openType;
    },
    onFakeSelect(item, index) {
      if (item.disabled || item.loading) return;
      this.onSelect(item, index);
    },
    onSelect(item) {
      const {  closeOnClickAction, canIUseGetUserProfile } = this;
      if (item) {
        this.$emit('select', item);
        if (closeOnClickAction) {
          this.onClose();
        }
        if (item.openType === 'getUserInfo' && canIUseGetUserProfile) {
          uni.getUserProfile({
            desc: item.getUserProfileDesc || '  ',
            complete: (userProfile) => {
              this.$emit('getuserinfo', userProfile);
            },
          });
        }
      }
    },
    onCancel() {
      this.$emit('cancel');
    },
    onClose() {
      this.$emit('close');
    },
    onClickOverlay() {
      this.$emit('click-overlay');
      this.onClose();
    },
  },
};
